import React, { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import QvipleLoading from "../../../../../../../Loader/QvipleLoading";
import AssignBookMemberHeader from "./AssignBookMemberHeader";
import MemberItem from "./MemberItem";
import { useAssignSearchStaff } from "../../../../../../../Post/Institute/Components/search-api";

const SelectIssueStaffMember = ({ instituteId, onAddMember, onBack, step }) => {
  const [search, setSearch] = useState("");
  const [ref, inView] = useInView({ skip: search });
  const [allStudent, setAllStudent] = useState([]);
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);

  const { assignSearchStaff, assignSearchStaffRefetch } = useAssignSearchStaff({
    skip: !instituteId,
    search: {
      search: search,
      page: page,
      id: instituteId,
      limit: 10,
    },
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (instituteId) {
      setShowingDataLoading(true);
      assignSearchStaffRefetch();
    }
  }, [search, instituteId, page, assignSearchStaffRefetch]);

  useEffect(() => {
    if (search) {
      if (assignSearchStaff?.staff) {
        setAllStudent(assignSearchStaff?.staff);
        setShowingDataLoading(false);
      } else if (assignSearchStaff?.staff?.length === 0)
        setShowingDataLoading(false);
      else {
      }
    } else {
      if (assignSearchStaff?.staff) {
        if (page === 1) {
          setAllStudent(assignSearchStaff?.staff);
        } else {
          setAllStudent((prevState) =>
            [
              ...new Set(
                [...prevState, ...assignSearchStaff?.staff]?.map(JSON.stringify)
              ),
            ]?.map(JSON.parse)
          );
        }
        setShowingDataLoading(false);
      } else if (assignSearchStaff?.staff?.length === 0)
        setShowingDataLoading(false);
      else {
      }
    }
    if (assignSearchStaff?.staff?.length === 10) setState(true);
    else setState(false);
  }, [assignSearchStaff?.staff]);

  const onSearch = (val) => {
    setPage((page) => (page = 1));
    setSearch(val);
  };

  return (
    <>
      <AssignBookMemberHeader onBack={onBack} step={step} onSearch={onSearch} />
      {allStudent?.map((student, index) =>
        allStudent?.length === index + 1 ? (
          <div ref={ref} key={student?._id}>
            <MemberItem student={student} onAddMember={onAddMember} isStaff />
          </div>
        ) : (
          <MemberItem
            key={student?._id}
            student={student}
            onAddMember={onAddMember}
            isStaff
          />
        )
      )}
      {showingDataLoading && <QvipleLoading status="white" />}
    </>
  );
};

export default SelectIssueStaffMember;
