import { useEffect, useState } from "react";
import QLoader from "../../../../../Loader/QLoader";
import JoinFormInput from "../../../../../JoiningForm/Student/Form/JoinFormInput";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../../optimized/Ui/Button/CreateButton";
import { useUpdateTimeByLibrary } from "../../../../../hooks/member_tab/library-api";

const LibraryTiming = ({ lid, timing }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    from: "",
    to: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [updateTimeByLibrary] = useUpdateTimeByLibrary();
  useEffect(() => {
    if (timing) {
      setState((prev) => ({
        ...prev,
        from: timing?.from ?? "",
        to: timing?.to ?? "",
      }));
    }
  }, [timing]);

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onUpdate = () => {
    if (lid) {
      setDisabled((pre) => !pre);

      updateTimeByLibrary({
        lid: lid,
        libraryTime: {
          timing: {
            from: timeFormatTable(state?.from),
            to: timeFormatTable(state?.to),
          },
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "flex-start",
        gap: "2rem",
        padding: "1rem",
      }}
    >
      <JoinFormInput
        labelText={t("from")}
        placeholder="00:00"
        name={"from"}
        value={get24Format(state?.from)}
        type={"time"}
        onChange={onChange}
        customStyleContainer={{
          display: "flex",
          justifyContent: "flex-start",
          alignItems: "center",
          gap: "1rem",
        }}
      />
      <JoinFormInput
        labelText={t("to")}
        placeholder="00:00"
        name={"to"}
        value={get24Format(state?.to)}
        type={"time"}
        onChange={onChange}
        customStyleContainer={{
          display: "flex",
          justifyContent: "flex-start",
          alignItems: "center",
          gap: "1rem",
        }}
      />

      <CreateButton
        onAction={onUpdate}
        label={t("update")}
        customStyle={{
          margin: "0",
        }}
      />

      {disabled && <QLoader />}
    </div>
  );
};

export default LibraryTiming;

const timeFormatTable = (vale) => {
  const splitValue = vale?.split(":");
  let meridian = "";
  if (splitValue[0] > 12) {
    meridian = "pm";
    splitValue[0] -= 12;
  } else if (splitValue[0] < 12) {
    meridian = "am";
    if (splitValue[0] === 0) {
      splitValue[0] = 12;
    }
  } else {
    meridian = "pm";
  }
  const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
  let res = `${hour}:${splitValue[1]} ${meridian}`;
  return res?.substring(0, 8);
};

const get24Format = (data) => {
  let splt = data?.split(" ");
  let nt = splt?.[0]?.split(":");
  let hr = "";

  if (splt?.[1] === "Pm") {
    if (+nt?.[0] === 12) {
      hr = nt?.[0];
    } else {
      hr = +nt?.[0] + 12;
    }
  } else {
    hr = nt?.[0];
  }
  return `${hr}:${nt?.[1]}`;
};
