import style from "../LibraryFunctions/LibraryFunctions.module.css";
import { useTranslation } from "react-i18next";
import moment from "moment";
import { useLocation, useNavigate, useParams } from "react-router-dom";

const LibraryStocktakeCard = ({ history }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const getQuery = useLocation();
  const params = useParams();

  const onNavigate = (val, count) => {
    navigate(`/q/${params.username}/member/library?a=stocktake-detail`, {
      state: {
        ...getQuery.state,
        stid: history?._id,
        flow: val ?? "",
        count: count,
      },
    });
  };
  return (
    <div className={style.iohc_card}>
      <img
        src="/images/member_tab/subject/subject-daily-update-icon.svg"
        alt="icons"
      />
      <div className={style.iohc_card_container}>
        <div className={style.iohc_card_inner}>
          <h6
            style={{
              fontWeight: "500",
            }}
          >
            {moment(history?.created_at).format("DD/MM/YYYY")}
            {", "}
            {moment(history?.created_at).format("LT")}
          </h6>
        </div>
        <div className={style.iohc_btn}>
          <h6
            onClick={() =>
              onNavigate("book_isssue", history?.book_isssue_count)
            }
          >
            {t("book_issue")}
            {history?.book_isssue_count ?? 0}
          </h6>
          <h6 onClick={() => onNavigate("book_lost", history?.book_lost_count)}>
            {t("book_lost")}
            {history?.book_lost_count ?? 0}
          </h6>
          <h6
            onClick={() =>
              onNavigate("book_at_library", history?.book_at_library_count)
            }
          >
            {t("book_at_library")}
            {history?.book_at_library_count ?? 0}
          </h6>
          <h6
            onClick={() =>
              onNavigate("book_missing", history?.book_missing_count)
            }
          >
            {t("book_missing")}
            {history?.book_missing_count ?? 0}
          </h6>
        </div>
      </div>
    </div>
  );
};

export default LibraryStocktakeCard;
