import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";
import AllBooks from "./AllBooks/AllBooks";
import CollectedLogs from "./CollectedLogs/CollectedLogs";
import IssuedBooks from "./IssuedBooks/IssuedBooks";
import style from "./LibraryFunctions.module.css";
import LibraryMembers from "./LibraryMembers/LibraryMembers";
import LibraryCharge from "./LibraryCharges/LibraryCharge";
import LibraryExport from "./LibraryExport/LibraryExport";
import LibraryQrCodeGeneration from "../QrCode/LibraryQrCodeGeneration";
import LibraryLogs from "../LibraryLogs/LibraryLogs";
import LibraryStocktake from "../LibraryStocktake/LibraryStocktake";
import LibraryModerator from "../Moderator/LibraryModerator";
import { useGetLibraryDetail } from "../../../../../hooks/member_tab/library-api";
import LibraryStocktakeDetail from "../LibraryStocktake/LibraryStocktakeDetail";

function LibraryFunctions({ carryParentState, role, mid }) {
  const getQuery = useLocation();

  const { getLibraryDetail, getLibraryDetailRefetch } = useGetLibraryDetail({
    lid: carryParentState?.onClickValue?._id,
    skip: !carryParentState?.onClickValue?._id,
  });

  useEffect(() => {
    if (carryParentState?.onClickValue?._id) {
      getLibraryDetailRefetch();
    }
  }, [carryParentState?.onClickValue?._id]);

  return (
    <div className={style.admisionApplication}>
      <div className={style.applicationBody}>
        {getQuery.search?.substring(3, 7) === "book" && (
          <AllBooks
            getQuery={getQuery}
            carryParentState={carryParentState}
            fid={getLibraryDetail?.library?.institute?.financeDepart?.[0]}
            filterBy={getLibraryDetail?.library?.filter_by?.department}
            role={role}
            mid={mid}
          />
        )}
        {getQuery.search?.substring(3) === "issue" && (
          <IssuedBooks
            insId={getLibraryDetail?.library?.institute?._id}
            role={role}
            mid={mid}
          />
        )}
        {getQuery.search?.substring(3) === "collect" && <CollectedLogs />}
        {getQuery.search?.substring(3) === "member" && <LibraryMembers />}
        {getQuery.search?.substring(3, 9) === "charge" && (
          <LibraryCharge carryParentState={carryParentState} />
        )}
        {getQuery.search?.substring(3, 9) === "export" && (
          <LibraryExport
            carryParentState={carryParentState}
            lid={carryParentState?.onClickValue?._id}
          />
        )}
        {getQuery.search?.substring(3) === "qr" && (
          <LibraryQrCodeGeneration
            carryParentState={carryParentState}
            lid={carryParentState?.onClickValue?._id}
          />
        )}
        {getQuery.search?.substring(3) === "logs" && (
          <LibraryLogs
            carryParentState={carryParentState}
            lid={carryParentState?.onClickValue?._id}
          />
        )}
        {getQuery.search?.substring(3) === "stocktake" && (
          <LibraryStocktake
            carryParentState={carryParentState}
            lid={carryParentState?.onClickValue?._id}
          />
        )}
        {getQuery.search?.substring(3) === "stocktake-detail" && (
          <LibraryStocktakeDetail
            carryParentState={carryParentState}
            lid={carryParentState?.onClickValue?._id}
          />
        )}
        {getQuery.search?.substring(3) === "moderator" && (
          <LibraryModerator
            // carryParentState={carryParentState}
            lid={carryParentState?.onClickValue?._id}
            instituteId={getLibraryDetail?.library?.institute?._id}
            fid={getLibraryDetail?.library?.institute?.financeDepart?.[0]}
          />
        )}
      </div>
    </div>
  );
}

export default LibraryFunctions;
