import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useNavigate } from "react-router-dom";
import QvipleLoading from "../../../../../Loader/QvipleLoading";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import LibraryAddModerator from "./AddLibraryModerator";
import style from "./LibraryModerator.module.css";
import LibraryModeratoMenu from "./LibraryModeratoMenu";
import LibraryModeratorCard from "./LibraryModeratorCard";
import { useScrollbar } from "../../../../../Scroll/use-scroll";
import { useGetModeratorByLibrary } from "../../../../../hooks/member_tab/library-api";
const LibraryModerator = ({ lid, instituteId, fid }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [addModerator, setAddModerator] = useState(false);
  const [openModeratorMenu, setOpenModeratorMenu] = useState("");
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [refetchStatus, setRefetchStatus] = useState(false);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [moderatorList, setModeratorList] = useState([]);

  const { getModeratorByLibrary, getModeratorByLibraryRefetch } =
    useGetModeratorByLibrary({
      data: {
        lid: lid,
        page: page,
        limit: 10,
      },
      skip: !lid,
    });
  const { scrollToTop } = useScrollbar();
  useEffect(() => {
    scrollToTop();
  }, []);
  useEffect(() => {
    if (lid) {
      setShowingDataLoading(true);
      getModeratorByLibraryRefetch();
    }
  }, [lid, page, getModeratorByLibraryRefetch]);
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (refetchStatus) {
      setModeratorList(getModeratorByLibrary?.moderator);
      setRefetchStatus(false);
      setShowingDataLoading(false);
    } else {
      if (getModeratorByLibrary?.moderator) {
        setModeratorList((prevState) =>
          [
            ...new Set(
              [...prevState, ...getModeratorByLibrary?.moderator]?.map(
                JSON.stringify
              )
            ),
          ]?.map(JSON.parse)
        );
        setShowingDataLoading(false);
      } else if (getModeratorByLibrary?.moderator?.length === 0)
        setShowingDataLoading(false);
      else {
      }
    }

    if (getModeratorByLibrary?.moderator?.length === 10) setState(true);
    else setState(false);
  }, [getModeratorByLibrary?.moderator]);

  const onBackWithArrowLeft = () => {
    navigate(-1);
  };
  const onRefetchWhenAdd = () => {
    setRefetchStatus(true);
    setPage(1);
    setShowingDataLoading(true);
    getModeratorByLibraryRefetch();
  };
  // console.info("this s moderator", carryParentState);
  return (
    <div className={style.moderator_container}>
      <section className={style.moderator_container_title}>
        <img
          src="/images/arrow-left-fees-icon.svg"
          onClick={onBackWithArrowLeft}
          alt="go to previous tab"
          title="Go Back"
          style={{
            cursor: "pointer",
          }}
        />
        <h6>{t("library_moderator")}</h6>
      </section>
      <BorderBottom
        customStyle={{
          width: "100%",
        }}
      />
      <h6
        className={style.add_new_moderator}
        onClick={() => setAddModerator(true)}
      >
        {t("add_new_moderator")}
      </h6>
      <div className={style.refund_list}>
        {moderatorList?.map((moderator, index) =>
          moderatorList?.length === index + 1 ? (
            <div ref={ref} key={moderator?._id}>
              <LibraryModeratorCard
                moderator={moderator}
                setOpenModeratorMenu={setOpenModeratorMenu}
              />
            </div>
          ) : (
            <LibraryModeratorCard
              moderator={moderator}
              key={moderator?._id}
              setOpenModeratorMenu={setOpenModeratorMenu}
            />
          )
        )}
        {addModerator && (
          <LibraryAddModerator
            onClose={() => setAddModerator((pre) => !pre)}
            instituteId={instituteId}
            lid={lid}
            onRefetchWhenAdd={onRefetchWhenAdd}
            fid={fid}
          />
        )}
        {openModeratorMenu && (
          <LibraryModeratoMenu
            lid={lid}
            instituteId={instituteId}
            onRefetch={onRefetchWhenAdd}
            onClose={() => setOpenModeratorMenu("")}
            openModeratorMenu={openModeratorMenu}
            fid={fid}
          />
        )}
        {showingDataLoading && <QvipleLoading />}
      </div>
    </div>
  );
};

export default LibraryModerator;
