import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import AssignStaffModal from "../../../../../Department/Institute/AssignStaff/AssignStaffModal";
import CustomNormalSelectInput from "../../../../../JoiningForm/Student/Form/CustomNormalSelectInput";
import QLoader from "../../../../../Loader/QLoader";
import { imageShowUrl } from "../../../../../services/BaseUrl";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import style from "./LibraryModerator.module.css";
import PreviewLibraryModerator from "./PreviewLibraryModerator";
import { useAddModeratorByLibrary } from "../../../../../hooks/member_tab/library-api";
import PopupWrapper from "../../FinanceManager/FeesStructure/PopupWrapper";
import FeeStatisticsDepartmentFilter from "../../../../../optimized/UserStaffMember/FinanceManager/FeeStatistics/FeeStatisticsDepartmentFilter";

const AddLibraryModerator = ({
  instituteId,
  lid,
  onClose,
  onRefetchWhenAdd,
  openModeratorMenu,
  openAs,
  onEdit,
  fid,
}) => {
  const { t } = useTranslation();
  const [activePopup, setActivePopup] = useState(
    openAs === "EDIT" ? "PREVIEW_FORM" : "OPEN_STAFF_LIST"
  );
  const [selectedStaff, setSelectedStaff] = useState(
    openAs === "EDIT" ? openModeratorMenu?.access_staff : ""
  );
  const [moderatorState, setModeratorState] = useState({
    mod_role: openAs === "EDIT" ? openModeratorMenu?.access_role : "",
    sid: openAs === "EDIT" ? openModeratorMenu?.access_staff?._id : "",
  });
  const [department, setDepartment] = useState({
    id: [],
    depart: [],
  });
  const [addModeratorByLibrary] = useAddModeratorByLibrary();

  useEffect(() => {
    if (openAs === "EDIT") {
      setSelectedStaff(openModeratorMenu?.access_staff);
      setModeratorState({
        mod_role: openModeratorMenu?.access_role,
        sid: openModeratorMenu?.access_staff?._id,
      });
      let arr = [];
      let arr1 = [];
      for (let dpt of openModeratorMenu?.department) {
        arr.push(dpt?._id);
        arr1.push(dpt);
      }
      setDepartment({
        id: arr,
        depart: arr1,
      });
      setActivePopup("PREVIEW_FORM");
    }
  }, [openAs]);
  const onSelectStaff = (staff) => {
    setSelectedStaff(staff);
    setModeratorState((prev) => ({
      ...prev,
      sid: staff?._id,
    }));
    if (openAs === "EDIT") {
      setActivePopup("PREVIEW_FORM");
    } else {
      setActivePopup("ASSIGN_ROLE");
    }
  };

  const onBackWithArrowLeft = () => {
    setActivePopup("OPEN_STAFF_LIST");
  };
  // console.info("this is staff sleected", selectedStaff);

  const onSelectRole = (val) => {
    setModeratorState((prev) => ({
      ...prev,
      mod_role:
        val === "As Librarian"
          ? "FULL_FUNCTIONALITY"
          : val === "Department Wise Librarian"
          ? "DEPARTMENT_WISE"
          : val === "Library Fines"
          ? "FINE_CHARGE"
          : "",
    }));
  };

  const onAssignModerator = () => {
    if (lid && moderatorState.mod_role && moderatorState.sid) {
      setActivePopup("LOADING");
      addModeratorByLibrary({
        lid: lid,
        addModerator: {
          ...moderatorState,
          depart: department.id,
        },
      })
        .then(() => {
          onRefetchWhenAdd();
          setActivePopup("");
          onClose();
        })
        .catch({});
    }
  };

  const onUpdateModerator = () => {
    onEdit({
      ...moderatorState,
      depart: department.id,
    });
    onClose();
  };
  const onCloseEdit = () => {
    onClose();
  };

  const onSelectDepartment = (val) => {
    if (department?.id?.includes(val?._id)) {
      setDepartment((prev) => ({
        id: prev?.id?.filter((id) => id !== val?._id),
        data: prev?.data?.filter((id) => id?._id !== val?._id),
      }));
    } else {
      if (department.id?.length > 0)
        setDepartment((prev) => ({
          id: [...prev?.id, val?._id],
          data: [...prev?.data, val],
        }));
      else
        setDepartment(() => ({
          id: [val?._id],
          data: [val],
        }));
    }
  };
  // console.info("sjfhdbsd", department);

  return (
    <>
      {activePopup === "OPEN_STAFF_LIST" && (
        <AssignStaffModal
          assignHead={activePopup === "OPEN_STAFF_LIST" ? true : false}
          headSelect={onSelectStaff}
          title={t("staff_list")}
          onClose={onClose}
          instituteId={instituteId}
          viewAs="ADMISSION_ADMIN"
        />
      )}
      {activePopup === "ASSIGN_ROLE" && (
        <>
          <PopupWrapper onClose={onBackWithArrowLeft}>
            <div className={style.assign_role_container}>
              <section className={style.assign_role_title}>
                <img
                  src="/images/arrow-left-fees-icon.svg"
                  onClick={onBackWithArrowLeft}
                  alt="back icon"
                  style={{
                    cursor: "pointer",
                  }}
                />

                <h6>{t("assign_role_to_moderator")}</h6>
              </section>
              <BorderBottom
                customStyle={{
                  width: "100%",
                }}
              />

              <section className={style.selected_staff_container}>
                <img
                  src={
                    selectedStaff?.staffProfilePhoto
                      ? `${imageShowUrl}/${selectedStaff?.staffProfilePhoto}`
                      : "/images/member_tab/class/default_avatar.svg"
                  }
                  alt="staff profile avatar"
                />
                <div className={style.selected_staff_text}>
                  <h6>
                    {`${selectedStaff?.staffFirstName} ${
                      selectedStaff?.staffMiddleName ?? ""
                    } ${selectedStaff?.staffLastName}`}
                  </h6>
                  <p>
                    {t("index_number")} {": "}
                    {selectedStaff?.staffROLLNO}
                  </p>
                </div>
              </section>

              <section
                style={{
                  padding: "0.8rem",
                }}
              >
                <CustomNormalSelectInput
                  selectLabel={t("select_role_of_moderator")}
                  selectedValue={t("please_select_role")}
                  options={[
                    "As Librarian",
                    "Department Wise Librarian",
                    "Library Fines",
                  ]}
                  value={
                    moderatorState?.mod_role === "FULL_FUNCTIONALITY"
                      ? "As Librarian"
                      : moderatorState?.mod_role === "DEPARTMENT_WISE"
                      ? "Department Wise Librarian"
                      : moderatorState?.mod_role === "FINE_CHARGE"
                      ? "Library Fines"
                      : ""
                  }
                  onClick={onSelectRole}
                />
                {moderatorState.mod_role === "DEPARTMENT_WISE" ? (
                  <FeeStatisticsDepartmentFilter
                    fid={fid}
                    onSelect={onSelectDepartment}
                    onlyDepartment
                    defalutValue={
                      department?.id?.length > 1
                        ? `${department?.id?.length} selected`
                        : department?.dName ?? ""
                    }
                  />
                ) : null}

                <button
                  className={style.assign_btn}
                  onClick={onAssignModerator}
                >
                  {t("assign")}{" "}
                </button>
              </section>
            </div>
          </PopupWrapper>
        </>
      )}
      {activePopup === "PREVIEW_FORM" && (
        <PreviewLibraryModerator
          onCloseEdit={onCloseEdit}
          setActivePopup={setActivePopup}
          selectedStaff={selectedStaff}
          moderatorState={moderatorState}
          onSelectRole={onSelectRole}
          onUpdateModerator={onUpdateModerator}
        />
      )}
      {activePopup === "LOADING" && <QLoader />}
    </>
  );
};

export default AddLibraryModerator;
