import React from "react";
import style from "./AssignBookMember.module.css";
import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../../../../../services/BaseUrl";

function MemberItem({ student, onAddMember, isStaff }) {
  const { t } = useTranslation();

  return (
    <div className={style.issuedBookItem} onClick={() => onAddMember(student)}>
      <div className={style.issuedBookItemMidleft}>
        <div className={style.imgcontainer}>
          {isStaff ? (
            <img
              src={
                student?.staffProfilePhoto
                  ? `${imageShowUrl}/${student?.staffProfilePhoto}`
                  : "/images/member_tab/class/default_avatar.svg"
              }
              alt=""
            />
          ) : (
            <img
              src={
                student?.studentProfilePhoto
                  ? `${imageShowUrl}/${student?.studentProfilePhoto}`
                  : "/images/member_tab/class/default_avatar.svg"
              }
              alt=""
            />
          )}
        </div>
      </div>
      <div className={style.issuedBookItemMidright}>
        {isStaff ? (
          <>
            <h6>
              {`${student?.staffFirstName} ${
                student?.staffMiddleName ? student?.staffMiddleName : ""
              } ${student?.staffLastName}`}
            </h6>
            <p>
              {t("index")} {student?.staffROLLNO}
            </p>
          </>
        ) : (
          <>
            <h6>
              {`${student?.studentFirstName} ${
                student?.studentMiddleName ? student?.studentMiddleName : ""
              } ${student?.studentLastName}`}
            </h6>
            <p>
              {t("gr_no")} {student?.studentGRNO}
            </p>
          </>
        )}
      </div>
    </div>
  );
}

export default MemberItem;
