import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import style from "../../../StaffAdmisionAdmin/Refund/AdmisisonRefund.module.css";
import { assestsUserFinanceUrl } from "../../../../../../optimized/Services/UrlConfig/AssestsBaseUrl";
import { useEffect, useState } from "react";
import UserMemberContentWrapper from "../../../../../../optimized/Ui/UserMember/UserMemberContentWrapper";
import {
  useLibraryAllExcelExport,
  useLibraryMemberExcelExport,
} from "../../../../../../hooks/member_tab/library-api";
import { useInView } from "react-intersection-observer";
import { useScrollbar } from "../../../../../../Scroll/use-scroll";
import QvipleLoading from "../../../../../../Loader/QvipleLoading";
import moment from "moment";
import LibraryExportModal from "../../../../../../optimized/UserStaffMember/LibraryManager/Export/LibraryExportModal";
import QLoader from "../../../../../../Loader/QLoader";

const LibraryExport = ({ lid }) => {
  const { t } = useTranslation();
  const [openFilter, setOpenFilter] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [refundList, setRefundList] = useState([]);
  const [libraryMemberExcelExport] = useLibraryMemberExcelExport();

  const { libraryAllExcelExport, libraryAllExcelExportRefetch } =
    useLibraryAllExcelExport({
      data: {
        lid: lid,
        page: page,
        limit: 10,
      },
      skip: !lid,
    });
  const { scrollToTop } = useScrollbar();
  useEffect(() => {
    scrollToTop();
  }, []);
  useEffect(() => {
    if (lid) {
      setShowingDataLoading(true);
      libraryAllExcelExportRefetch();
    }
  }, [lid, page, libraryAllExcelExportRefetch]);
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (libraryAllExcelExport?.excel_arr) {
      setRefundList((prevState) =>
        [
          ...new Set(
            [...prevState, ...libraryAllExcelExport?.excel_arr]?.map(
              JSON.stringify
            )
          ),
        ]?.map(JSON.parse)
      );
      setShowingDataLoading(false);
    } else if (libraryAllExcelExport?.excel_arr?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (libraryAllExcelExport?.excel_arr?.length === 10) setState(true);
    else setState(false);
  }, [libraryAllExcelExport?.excel_arr]);

  const onCloseFilter = () => {
    setOpenFilter("");
  };
  const onBookFilter = () => {
    setOpenFilter("BOOK");
  };
  const onIssueFilter = () => {
    setOpenFilter("ISSUE");
  };
  const onCollectFilter = () => {
    setOpenFilter("COLLECT");
  };
  const onEntryLogFilter = () => {
    setOpenFilter("ENTRY_LOG");
  };
  const onBookReviewFilter = () => {
    setOpenFilter("REVIEW");
  };
  const onMemberFilter = () => {
    if (lid) {
      setDisabled((pre) => !pre);
      libraryMemberExcelExport({
        lid: lid,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div
          className={style.refund_container_title}
          style={{
            justifyContent: "space-between",
          }}
        >
          <h6>{t("data_export")}</h6>
          <div className={style.both_filter}>
            <div onClick={onBookFilter} className={style.both_filter_with_name}>
              <img
                src={`${assestsUserFinanceUrl}/filter.svg`}
                alt="filter icon"
              />
              <h6>{t("books")}</h6>
            </div>
            <div
              onClick={onCollectFilter}
              className={style.both_filter_with_name}
            >
              <img
                src={`${assestsUserFinanceUrl}/filter.svg`}
                alt="filter icon"
              />
              <h6>{t("collected")}</h6>
            </div>

            <div
              onClick={onIssueFilter}
              className={style.both_filter_with_name}
            >
              <img
                src={`${assestsUserFinanceUrl}/filter.svg`}
                alt="filter icon"
              />
              <h6>{t("issued")}</h6>
            </div>
            <div
              onClick={onMemberFilter}
              className={style.both_filter_with_name}
            >
              <img
                src={`${assestsUserFinanceUrl}/filter.svg`}
                alt="filter icon"
              />
              <h6>{t("members")}</h6>
            </div>
            <div
              onClick={onEntryLogFilter}
              className={style.both_filter_with_name}
            >
              <img
                src={`${assestsUserFinanceUrl}/filter.svg`}
                alt="filter icon"
              />
              <h6>{t("entry_logs")}</h6>
            </div>
            <div
              onClick={onBookReviewFilter}
              className={style.both_filter_with_name}
            >
              <img
                src={`${assestsUserFinanceUrl}/filter.svg`}
                alt="filter icon"
              />
              <h6>{t("book_review")}</h6>
            </div>
          </div>
        </div>
        <BorderBottom
          customStyle={{
            width: "100%",
            marginTop: "0.2rem",
          }}
        />
        <div className={style.refund_list}>
          {refundList?.map((excel, index) =>
            refundList?.length === index + 1 ? (
              <a
                ref={ref}
                key={excel?._id}
                className={style.export_card}
                title="Download File"
                href={`https://d3dqpu54js2vfl.cloudfront.net/${excel?.excel_file}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={`${assestsUserFinanceUrl}/xsxl.svg`}
                  alt="excel icon"
                />
                <section className={style.export_card_text}>
                  <h6>{excel?.excel_file_name ?? ""}</h6>
                  <div className={style.export_card_date}>
                    <h6>{moment(excel?.created_at).format("DD MMM yyy")}</h6>
                    <h6>{moment(excel?.created_at).format("LT")}</h6>
                  </div>
                </section>
              </a>
            ) : (
              <a
                key={excel?._id}
                className={style.export_card}
                title="Download File"
                href={`https://d3dqpu54js2vfl.cloudfront.net/${excel?.excel_file}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={`${assestsUserFinanceUrl}/xsxl.svg`}
                  alt="excel icon"
                />
                <section className={style.export_card_text}>
                  <h6>{excel?.excel_file_name ?? ""}</h6>
                  <div className={style.export_card_date}>
                    <h6>{moment(excel?.created_at).format("DD MMM yyy")}</h6>
                    <h6>{moment(excel?.created_at).format("LT")}</h6>
                  </div>
                </section>
              </a>
            )
          )}

          {showingDataLoading && <QvipleLoading />}
        </div>
      </UserMemberContentWrapper>
      {openFilter ? (
        <LibraryExportModal
          onClose={onCloseFilter}
          openFilter={openFilter}
          lid={lid}
        />
      ) : null}
      {disabled && <QLoader />}
    </>
  );
};

export default LibraryExport;
