import React, { useEffect, useState } from "react";
import style from "../LibraryFunctions/LibraryFunctions.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { useLibraryGetStocktakeHistory } from "../../../../../hooks/member_tab/library-api";
import { useInView } from "react-intersection-observer";
import QvipleLoading from "../../../../../Loader/QvipleLoading";
import LibraryStocktakeCard from "./LibraryStocktakeCard";
const LibraryStocktake = ({ lid }) => {
  const { t } = useTranslation();
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [list, setList] = useState([]);
  const { libraryGetStocktakeHistory, libraryGetStocktakeHistoryRefetch } =
    useLibraryGetStocktakeHistory({
      data: {
        lid: lid,
        page: page,
        limit: 10,
      },
      skip: !lid,
    });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (lid) {
      setShowingDataLoading(true);
      libraryGetStocktakeHistoryRefetch();
    }
  }, [page, lid, libraryGetStocktakeHistoryRefetch]);

  useEffect(() => {
    if (libraryGetStocktakeHistory?.stocktake) {
      if (page > 1) {
        setList((prevState) =>
          [
            ...new Set(
              [...prevState, ...libraryGetStocktakeHistory?.stocktake]?.map(
                JSON.stringify
              )
            ),
          ]?.map(JSON.parse)
        );
        setShowingDataLoading(false);
      } else {
        setList(libraryGetStocktakeHistory?.stocktake);
        setShowingDataLoading(false);
      }
    } else if (libraryGetStocktakeHistory?.stocktake?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (libraryGetStocktakeHistory?.stocktake?.length === 10) setState(true);
    else setState(false);
  }, [libraryGetStocktakeHistory?.stocktake]);
  return (
    <>
      <h6 className={style.fine_heading}>{t("stocktake")}</h6>
      <BorderBottom
        customStyle={{
          width: "100%",
          marginTop: "0.2rem",
        }}
      />

      <div
        style={{
          padding: "1rem",
        }}
      >
        {list?.map((history, index) =>
          list?.length === index + 1 ? (
            <div
              key={history?._id}
              ref={ref}
              style={{
                width: "100%",
              }}
            >
              <LibraryStocktakeCard history={history} />
            </div>
          ) : (
            <LibraryStocktakeCard history={history} key={history?._id} />
          )
        )}

        {showingDataLoading && <QvipleLoading />}
        {!showingDataLoading &&
          libraryGetStocktakeHistory?.stocktake?.length === 0 && (
            <div className={style.noData}>
              <img
                src="/images/nodata.jpg"
                style={{
                  width: "100%",
                }}
                alt=""
              />
            </div>
          )}
      </div>
    </>
  );
};

export default LibraryStocktake;
