import React from "react";
import style from "./CollectedLogs.module.css";
import moment from "moment";
import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../../../../services/BaseUrl";

function CollectedLogsCard({ book }) {
  const { t } = useTranslation();
  return (
    <div className={style.clc_card}>
      <div className={style.clc_card_inner}>
        <img
          className={style.bookItemImg}
          src={
            book?.book?.photo
              ? `${imageShowUrl}/${book?.book?.photo}`
              : "/images/defaultBook.png"
          }
        />
        <div className={style.clc_card_inner_text}>
          <h6>
            {" "}
            {book?.book?.bookName?.length >= 18
              ? `${book?.book?.bookName?.match(/.{1,18}/g)[0]} ...`
              : book?.book?.bookName}
          </h6>
          <p>
            {t("accession_number")} {": "}
            {book?.book?.accession_number ?? ""}
          </p>
          <p>
            {t("issue_date")}
            {": "}
            {moment(book?.issuedDate).format("Do MMM YYYY")} {", "}
            {moment(book?.issuedDate).format("LT")}
          </p>
        </div>
      </div>
      {book?.staff_member ? (
        <div className={style.clc_card_inner}>
          <img
            className={style.bookItemImg}
            src={
              book?.staff_member?.staffProfilePhoto
                ? `${imageShowUrl}/${book?.staff_member?.staffProfilePhoto}`
                : "/images/member_tab/class/default_avatar.svg"
            }
          />
          <div className={style.clc_card_inner_text}>
            <h6>
              {`${book?.staff_member?.staffFirstName} ${
                book?.staff_member?.staffMiddleName
                  ? book?.staff_member?.staffMiddleName
                  : ""
              } ${book?.staff_member?.staffLastName}`}
            </h6>
            <p>
              {t("index_number")} {": "}
              {book?.staff_member?.staffROLLNO ?? ""}
            </p>
            <p>
              {t("rcv_date")}
              {": "}
              {moment(book?.createdAt).format("Do MMM YYYY")} {", "}
              {moment(book?.createdAt).format("LT")}
            </p>
          </div>
        </div>
      ) : (
        <div className={style.clc_card_inner}>
          <img
            className={style.bookItemImg}
            src={
              book?.member?.studentProfilePhoto
                ? `${imageShowUrl}/${book?.member?.studentProfilePhoto}`
                : "/images/member_tab/class/default_avatar.svg"
            }
          />
          <div className={style.clc_card_inner_text}>
            <h6>
              {`${book?.member?.studentFirstName} ${
                book?.member?.studentMiddleName
                  ? book?.member?.studentMiddleName
                  : ""
              } ${book?.member?.studentLastName}`}
            </h6>
            <p>
              {t("gr_number")} {": "}
              {book?.member?.studentGRNO ?? ""}
            </p>
            <p>
              {t("rcv_date")}
              {": "}
              {moment(book?.createdAt).format("Do MMM YYYY")} {", "}
              {moment(book?.createdAt).format("LT")}
            </p>
          </div>
        </div>
      )}
    </div>
  );
}

export default CollectedLogsCard;
