import React from "react";
import style from "./LibraryHead.module.css";
import LibraryFunctions from "./LibraryFunctions/LibraryFunctions";
import LibraryProfile from "./LibraryProfile";
import { useLocation } from "react-router-dom";

function LibraryHead({ onClickValue, carryParentState, role, mid }) {
  const getQuery = useLocation();

  // console.info("sdbfhjsdbhgbs", carryParentState);
  return (
    <div className={style.departmenthead}>
      {!getQuery.search && (
        <LibraryProfile carryParentState={carryParentState} role={role} />
      )}

      {getQuery.search && (
        <LibraryFunctions
          carryParentState={carryParentState}
          insId={onClickValue?.institute}
          role={role}
          mid={mid}
        />
      )}
    </div>
  );
}

export default LibraryHead;
