import React, { useState } from "react";
import style from "./AssignBookMember.module.css";
import PopupWrapper from "../../../../FinanceManager/FeesStructure/PopupWrapper";
import QLoader from "../../../../../../../Loader/QLoader";
import SelectIssueMember from "./SelectIssueMember";
import SelectIssueBook from "./SelectIssueBook";
import { useSelector } from "react-redux";
import {
  useIssueBook,
  useIssueStaffBook,
} from "../../../../../../../hooks/member_tab/library-api";
import AssignIssuePreview from "./AssignIssuePreview";
import SelectIssueStaffMember from "./SelectIssueStaffMember";
const AssignBookMember = ({
  onClose,
  instituteId,
  onRefetchWhenBookIssue,
  isView,
  role,
  mid,
}) => {
  const libAuthor = useSelector((state) => state.libraryChange);
  const [step, setStep] = useState(0);
  const [disabled, setDisabled] = useState(false);
  const [member, setMember] = useState("");
  const [book, setBook] = useState("");
  const [issueBook] = useIssueBook();
  const [issueStaffBook] = useIssueStaffBook();

  const onBack = () => {
    if (step === 0) {
      onClose();
    } else if (step === 1) {
      setStep(0);
    } else {
      setStep(1);
    }
  };

  const onAddMember = (val) => {
    setMember(val);
    setStep(1);
  };
  const onAddBook = (val) => {
    setBook(val);
    setStep(2);
  };

  const onIssueBook = (data) => {
    if (libAuthor?.lid && member?._id && book?._id) {
      setDisabled(true);
      if (isView === "STAFF") {
        issueStaffBook({
          lid: libAuthor?.lid,
          issueData: {
            memberId: member?._id,
            bookId: book?._id,
            flow: "Normal",
          },
        })
          .then(() => {
            onRefetchWhenBookIssue();
            onClose(false);
            setDisabled(false);
          })
          .catch(() => {});
      } else {
        issueBook({
          lid: libAuthor?.lid,
          issueData: {
            memberId: member?._id,
            bookId: book?._id,
            flow: "Normal",
            ...data,
          },
        })
          .then(() => {
            onRefetchWhenBookIssue();
            onClose(false);
            setDisabled(false);
          })
          .catch(() => {});
      }
    }
  };

  return (
    <>
      <PopupWrapper onClose={onBack}>
        <div className={style.issue_modal}>
          {step === 0 &&
            (isView === "STAFF" ? (
              <SelectIssueStaffMember
                instituteId={instituteId}
                onAddMember={onAddMember}
                step={step}
                onBack={onBack}
              />
            ) : (
              <SelectIssueMember
                instituteId={instituteId}
                onAddMember={onAddMember}
                step={step}
                onBack={onBack}
              />
            ))}
          {step === 1 && (
            <SelectIssueBook
              onAddBook={onAddBook}
              step={step}
              onBack={onBack}
              member={member}
              isView={isView}
              role={role}
              mid={mid}
            />
          )}
          {step === 2 && (
            <AssignIssuePreview
              step={step}
              onBack={onBack}
              book={book}
              member={member}
              onIssueBook={onIssueBook}
              isView={isView}
            />
          )}
        </div>
        {disabled && <QLoader />}
      </PopupWrapper>
    </>
  );
};

export default AssignBookMember;
