import React, { useEffect, useState } from "react";
import style from "../LibraryFunctions/LibraryFunctions.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { Notfication } from "../../../../../validation/Snackbar";
import {
  useGetLibraryDetail,
  useLibraryGenerateBookQr,
  useLibraryGenerateMemberQr,
  useLibraryGetGeneratedBookQrList,
  useLibraryGetGeneratedStaffQrList,
  useLibraryGetGeneratedStudentQrList,
} from "../../../../../hooks/member_tab/library-api";
import QLoader from "../../../../../Loader/QLoader";
import LibraryTiming from "./LibraryTiming";
const LibraryQrCodeGeneration = ({ lid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
  });

  const [libraryGenerateMemberQr] = useLibraryGenerateMemberQr();
  const [libraryGenerateBookQr] = useLibraryGenerateBookQr();
  const [libraryGetGeneratedStaffQrList] = useLibraryGetGeneratedStaffQrList();
  const [libraryGetGeneratedStudentQrList] =
    useLibraryGetGeneratedStudentQrList();
  const [libraryGetGeneratedBookQrList] = useLibraryGetGeneratedBookQrList();
  const { getLibraryDetail, getLibraryDetailRefetch } = useGetLibraryDetail({
    lid: lid,
    skip: !lid,
  });

  useEffect(() => {
    if (lid) {
      getLibraryDetailRefetch();
    }
  }, [lid, getLibraryDetailRefetch]);
  //   const []=useLibraryGetGeneratedBookQrList()

  const onMemberGenerate = (val) => {
    if (lid) {
      setDisabled((pre) => !pre);
      libraryGenerateMemberQr({
        lid: lid,
        flow: val,
      }).then((res) => {
        setNotificationState({
          msg: res?.data?.message,
          run: true,
        });
        setDisabled((pre) => !pre);
      });
    }
  };
  const onBookGenerate = () => {
    if (lid) {
      setDisabled((pre) => !pre);
      libraryGenerateBookQr({
        lid: lid,
      }).then((res) => {
        setNotificationState({
          msg: res?.data?.message,
          run: true,
        });
        setDisabled((pre) => !pre);
      });
    }
  };

  const onBookQRPdfGenerate = () => {
    if (lid) {
      setDisabled((pre) => !pre);

      libraryGetGeneratedBookQrList({
        lid: lid,
      })
        .then((res) => {
          setDisabled((pre) => !pre);

          if (res?.data?.book_qr?.length > 0) {
            setNotificationState({
              msg: "All Book Qr code pdf in process. It will take some time.",
              run: true,
            });
            const workerInstance = new Worker(
              new URL(
                "../../../../../pdf/worker/main.book.pdf.worker.js",
                import.meta.url
              )
            );
            workerInstance.postMessage({
              book_qr: res?.data?.book_qr,
            });
            workerInstance.onmessage = (message) => {
              if (message) {
                window.open(message.data, "_blank");
                workerInstance.terminate();
              }
            };
            setNotificationState({
              msg: "All Book Qr code pdf  is complete",
              run: true,
            });
          }
        })
        .catch({});
    }
  };
  const onStaffQRPdfGenerate = () => {
    if (lid) {
      setDisabled((pre) => !pre);

      libraryGetGeneratedStaffQrList({
        lid: lid,
      }).then((res) => {
        setDisabled((pre) => !pre);

        if (res?.data?.all_staff?.length > 0) {
          setNotificationState({
            msg: "All Staff Qr code pdf in process. It will take some time.",
            run: true,
          });
          const workerInstance = new Worker(
            new URL(
              "../../../../../pdf/worker/main.book.pdf.worker.js",
              import.meta.url
            )
          );
          workerInstance.postMessage({
            all_staff: res?.data?.all_staff,
            isType: "STAFF",
          });
          workerInstance.onmessage = (message) => {
            if (message) {
              window.open(message.data, "_blank");
              workerInstance.terminate();
            }
          };
          setNotificationState({
            msg: "All Staff Qr code pdf  is complete",
            run: true,
          });
        }
      });
    }
  };
  const onStudentQRPdfGenerate = () => {
    if (lid) {
      setDisabled((pre) => !pre);

      libraryGetGeneratedStudentQrList({
        lid: lid,
      }).then((res) => {
        setDisabled((pre) => !pre);

        if (res?.data?.all_student?.length > 0) {
          setNotificationState({
            msg: "All Student Qr code pdf in process. It will take some time.",
            run: true,
          });
          const workerInstance = new Worker(
            new URL(
              "../../../../../pdf/worker/main.book.pdf.worker.js",
              import.meta.url
            )
          );
          workerInstance.postMessage({
            all_student: res?.data?.all_student,
            isType: "STUDENT",
          });
          workerInstance.onmessage = (message) => {
            if (message) {
              window.open(message.data, "_blank");
              workerInstance.terminate();
            }
          };
          setNotificationState({
            msg: "All Student Qr code pdf  is complete",
            run: true,
          });
        }
      });
    }
  };
  return (
    <>
      <h6 className={style.fine_heading}>{t("qr_code")}</h6>
      <BorderBottom
        customStyle={{
          width: "100%",
          marginTop: "0.2rem",
        }}
      />
      <div className={style.edn}>
        <div
          className={style.newBtn}
          onClick={() => onMemberGenerate("STUDENT")}
        >
          {t("student_qr")}
        </div>
        <div className={style.newBtn} onClick={() => onMemberGenerate("STAFF")}>
          {t("staff_qr")}
        </div>
        <div className={style.newBtn} onClick={onBookGenerate}>
          {t("book_qr")}
        </div>
        <div className={style.newBtn} onClick={onBookQRPdfGenerate}>
          {t("download_book_qr")}
        </div>
      </div>
      <div
        style={{
          marginBlock: "1rem",
        }}
      >
        <div className={style.iohc_card_inner}>
          <div
            className={style.iohc_card_inner}
            style={{
              flexDirection: "column",
              alignItems: "flex-start",
            }}
          >
            <h6
              className={style.fine_heading}
              style={{
                marginBottom: "1rem",
              }}
            >
              {t("library_qr_code")}
            </h6>

            {getLibraryDetail?.library?.qr_code ? (
              <img
                src={`${getLibraryDetail?.library?.qr_code}`}
                alt="qr icon"
                className={style.qr_image}
              />
            ) : null}
          </div>
          <div
            className={style.iohc_card_inner}
            style={{
              marginRight: "1rem",
            }}
          >
            <div
              className={style.newBtn}
              onClick={onStaffQRPdfGenerate}
              style={{
                width: "auto",
              }}
            >
              {t("download_staff_qr")}
            </div>
            <div
              className={style.newBtn}
              onClick={onStudentQRPdfGenerate}
              style={{
                width: "auto",
              }}
            >
              {t("download_student_qr")}
            </div>
          </div>
        </div>

        <LibraryTiming lid={lid} timing={getLibraryDetail?.library?.timing} />
      </div>
      {notificationState.run && (
        <Notfication
          msg={notificationState.msg}
          run={notificationState.run}
          setRun={() => setNotificationState({ msg: "", run: false })}
          postiton="bottom"
          type="success"
        />
      )}
      {disabled && <QLoader />}
    </>
  );
};

export default LibraryQrCodeGeneration;
