import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import style from "./AssignBookMember.module.css";
import moment from "moment";
import { imageShowUrl } from "../../../../../../../services/BaseUrl";
import AssignBookMemberHeader from "./AssignBookMemberHeader";
import ItemBook from "./ItemBook";
import TileCard from "../../../../../../../optimized/Ui/Tab/Tile/TileCard";
import TileWrapper from "../../../../../../../optimized/Ui/Tab/Tile/TileWrapper";
import FormInput from "../../../../../../../optimized/Ui/Input/FormInput";

const AssignIssuePreview = ({
  member,
  book,
  onBack,
  step,
  onIssueBook,
  isView,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    for_days: 7,
    day_overdue_charge: "",
  });
  const [active, setActive] = useState("ONE_WEEK");
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onActivityTile = (val) => {
    setActive(val);
    setState((prev) => ({
      ...prev,
      for_days: val === "ONE_WEEK" ? 7 : val === "ONE_MONTH" ? 30 : "",
    }));
  };

  const onIssue = () => {
    onIssueBook({
      ...state,
    });
  };

  // console.info("dsbvfs", state);
  return (
    <>
      <AssignBookMemberHeader onBack={onBack} step={step} />

      <div className={style.issueBookk}>
        <h6>{t("member")}</h6>
        <div className={`${style.itembookk} ${style.bookmargin}`}>
          <img
            className={style.bluetick}
            src="/images/library/bluetick.svg"
            alt="verified"
          />
          <div className={style.issuedBookItemMidleft}>
            <div className={style.imgcontainer}>
              {isView === "STAFF" ? (
                <img
                  src={
                    member?.staffProfilePhoto
                      ? `${imageShowUrl}/${member?.staffProfilePhoto}`
                      : "/images/member_tab/class/default_avatar.svg"
                  }
                  alt=""
                />
              ) : (
                <img
                  src={
                    member?.studentProfilePhoto
                      ? `${imageShowUrl}/${member?.studentProfilePhoto}`
                      : "/images/member_tab/class/default_avatar.svg"
                  }
                  alt=""
                />
              )}
            </div>
          </div>
          <div className={style.issuedBookItemMidright}>
            {isView === "STAFF" ? (
              <>
                <h6>
                  {`${member?.staffFirstName} ${
                    member?.staffMiddleName ? member?.staffMiddleName : ""
                  } ${member?.staffLastName}`}
                </h6>
                <p>
                  {t("index")} {member?.staffROLLNO}
                </p>
              </>
            ) : (
              <>
                <h6>
                  {`${member?.studentFirstName} ${
                    member?.studentMiddleName ? member?.studentMiddleName : ""
                  } ${member?.studentLastName}`}
                </h6>
                <p>
                  {t("gr_no")} {member?.studentGRNO}
                </p>
              </>
            )}
          </div>
        </div>

        <h6>{t("book")}</h6>
        <ItemBook book={book} status="issueBook" />

        <h6 className={style.issuedDate}>
          {t("issue_date")} : <span>{moment().format("Do MMM YYYY")}</span>
        </h6>

        {isView !== "STAFF" ? (
          <>
            <TileWrapper label="duration">
              {tileList?.map((tile) => (
                <TileCard
                  key={tile?.key}
                  tile={tile}
                  onTile={onActivityTile}
                  activeTile={active}
                />
              ))}
            </TileWrapper>

            {active === "OTHER" ? (
              <FormInput
                label={t("how_many_days")}
                placeholder={t("how_many_days_placeholder")}
                name={"for_days"}
                value={state.for_days}
                type={"text"}
                onChange={onChange}
              />
            ) : null}
            <FormInput
              label={t("late_fees_per_day")}
              placeholder={t("late_fees_per_day_placeholder")}
              name={"day_overdue_charge"}
              value={state.day_overdue_charge}
              type={"text"}
              onChange={onChange}
              customFormInputContainer={{
                marginBottom: "1rem",
              }}
            />
          </>
        ) : null}

        {isView === "STAFF" ? (
          <button className={style.issuebtn} onClick={onIssueBook}>
            {t("issue_book")}
          </button>
        ) : (
          <button className={style.issuebtn} onClick={onIssue}>
            {t("issue_book")}
          </button>
        )}
      </div>
    </>
  );
};

export default AssignIssuePreview;

const tileList = [
  {
    key: "ONE_WEEK",
    name: "one_week",
  },
  {
    key: "ONE_MONTH",
    name: "one_months",
  },
  {
    key: "OTHER",
    name: "other",
  },
];
