import style from "../LibraryFunctions/LibraryFunctions.module.css";
import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../../../services/BaseUrl";
import { useStocktakeMissingBookStatus } from "../../../../../hooks/member_tab/library-api";
import { useState } from "react";
import QLoader from "../../../../../Loader/QLoader";

const LibraryStocktakeDetailCard = ({ book, isType, stid, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [stocktakeMissingBookStatus] = useStocktakeMissingBookStatus();

  const onMissingStatus = (status) => {
    if (stid && book?._id) {
      setDisabled((pre) => !pre);
      stocktakeMissingBookStatus({
        stid: stid,
        missingBook: {
          bookId: book?.book?._id,
          card_id: book?._id,
          status: status,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      {isType === "MISSING" ? (
        <div
          className={style.iohc_card}
          style={{
            justifyContent: "space-between",
          }}
        >
          <div className={style.iohcc_missing}>
            <img
              src={
                book?.book?.photo
                  ? `${imageShowUrl}/${book?.book?.photo}`
                  : "/images/defaultBook.png"
              }
              alt="icons"
              style={{
                width: "3rem",
                height: "3rem",
              }}
            />
            <div
              className={style.iohc_card_inner}
              style={{
                alignItems: "flex-start",
                flexDirection: "column",
                gap: "0",
              }}
            >
              <h6
                style={{
                  fontWeight: "500",
                }}
              >
                {t("book_name")} {": "}
                {book?.book?.bookName ?? ""}
              </h6>
              <p
                style={{
                  margin: "0",
                }}
              >
                {t("author_only")} {": "}
                {book?.book?.author ?? ""}
              </p>
              <p
                style={{
                  margin: "0",
                }}
              >
                {t("accession_number")} {": "}
                {book?.book?.accession_number ?? ""}
              </p>
            </div>
          </div>
          <div className={style.iohcb_missing}>
            <h6 onClick={() => onMissingStatus("book_isssue")}>
              {t("book_issue_only")}
            </h6>
            <h6 onClick={() => onMissingStatus("book_lost")}>
              {t("book_lost_only")}
            </h6>
            <h6 onClick={() => onMissingStatus("book_at_library")}>
              {t("book_at_library_only")}
            </h6>
          </div>
        </div>
      ) : (
        <div className={style.iohc_card}>
          <img
            src={
              book?.photo
                ? `${imageShowUrl}/${book?.photo}`
                : "/images/defaultBook.png"
            }
            alt="icons"
            style={{
              width: "3rem",
              height: "3rem",
            }}
          />
          <div
            className={style.iohc_card_inner}
            style={{
              alignItems: "flex-start",
              flexDirection: "column",
              gap: "0",
            }}
          >
            <h6
              style={{
                fontWeight: "500",
              }}
            >
              {t("book_name")} {": "}
              {book?.bookName ?? ""}
            </h6>
            <p
              style={{
                margin: "0",
              }}
            >
              {t("author_only")} {": "}
              {book?.author ?? ""}
            </p>
            <p
              style={{
                margin: "0",
              }}
            >
              {t("accession_number")} {": "}
              {book?.accession_number ?? ""}
            </p>
          </div>
        </div>
      )}
      {disabled && <QLoader />}
    </>
  );
};

export default LibraryStocktakeDetailCard;
