import React, { useEffect, useState } from "react";
import style from "../LibraryFunctions/LibraryFunctions.module.css";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import { useLibraryGetStocktakeOneHistory } from "../../../../../hooks/member_tab/library-api";
import { useInView } from "react-intersection-observer";
import QvipleLoading from "../../../../../Loader/QvipleLoading";
import LibraryStocktakeDetailCard from "./LibraryStocktakeDetailCard";
import { useLocation } from "react-router-dom";
const LibraryStocktakeDetail = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [list, setList] = useState([]);
  const {
    libraryGetStocktakeOneHistory,
    libraryGetStocktakeOneHistoryRefetch,
  } = useLibraryGetStocktakeOneHistory({
    data: {
      stid: getQuery.state?.stid,
      page: page,
      limit: 10,
      flow: getQuery.state?.flow,
    },
    skip: !getQuery.state?.stid,
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (getQuery.state?.stid) {
      setShowingDataLoading(true);
      libraryGetStocktakeOneHistoryRefetch();
    }
  }, [page, getQuery.state?.stid, libraryGetStocktakeOneHistoryRefetch]);

  useEffect(() => {
    if (libraryGetStocktakeOneHistory?.stocktake_list) {
      if (page > 1) {
        setList((prevState) =>
          [
            ...new Set(
              [
                ...prevState,
                ...libraryGetStocktakeOneHistory?.stocktake_list,
              ]?.map(JSON.stringify)
            ),
          ]?.map(JSON.parse)
        );
        setShowingDataLoading(false);
      } else {
        setList(libraryGetStocktakeOneHistory?.stocktake_list);
        setShowingDataLoading(false);
      }
    } else if (libraryGetStocktakeOneHistory?.stocktake_list?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (libraryGetStocktakeOneHistory?.stocktake_list?.length === 10)
      setState(true);
    else setState(false);
  }, [libraryGetStocktakeOneHistory?.stocktake_list]);

  const onRefetch = () => {
    setPage((prev) => (prev = 1));
    libraryGetStocktakeOneHistoryRefetch();
  };
  return (
    <>
      <h6 className={style.fine_heading}>
        {t("stocktake_detail")} ({t(getQuery.state?.flow)}{" "}
        {getQuery.state?.count ?? 0})
      </h6>
      <BorderBottom
        customStyle={{
          width: "100%",
          marginTop: "0.2rem",
        }}
      />
      <div
        style={{
          padding: "1rem",
        }}
      >
        {getQuery.state?.flow === "book_at_library" ||
        getQuery.state?.flow === "book_lost" ? (
          <>
            {list?.map((book, index) =>
              list?.length === index + 1 ? (
                <div
                  key={book?._id}
                  ref={ref}
                  style={{
                    width: "100%",
                  }}
                >
                  <LibraryStocktakeDetailCard book={book} />
                </div>
              ) : (
                <LibraryStocktakeDetailCard book={book} key={book?._id} />
              )
            )}
          </>
        ) : getQuery.state?.flow === "book_missing" ? (
          <>
            {list?.map((book, index) =>
              list?.length === index + 1 ? (
                <div
                  key={book?._id}
                  ref={ref}
                  style={{
                    width: "100%",
                  }}
                >
                  <LibraryStocktakeDetailCard
                    book={book}
                    isType="MISSING"
                    stid={getQuery.state?.stid}
                    onRefetch={onRefetch}
                  />
                </div>
              ) : (
                <LibraryStocktakeDetailCard
                  book={book}
                  key={book?._id}
                  isType="MISSING"
                  stid={getQuery.state?.stid}
                  onRefetch={onRefetch}
                />
              )
            )}
          </>
        ) : getQuery.state?.flow === "book_isssue" ? (
          <>
            {list?.map((book, index) =>
              list?.length === index + 1 ? (
                <div
                  key={book?._id}
                  ref={ref}
                  style={{
                    width: "100%",
                  }}
                >
                  <LibraryStocktakeDetailCard book={book} isType="ISSUE" />
                </div>
              ) : (
                <LibraryStocktakeDetailCard
                  book={book}
                  key={book?._id}
                  isType="ISSUE"
                />
              )
            )}
          </>
        ) : null}

        {showingDataLoading && <QvipleLoading />}
        {!showingDataLoading &&
          libraryGetStocktakeOneHistory?.stocktake_list?.length === 0 && (
            <div className={style.noData}>
              <img
                src="/images/nodata.jpg"
                alt=""
                style={{
                  width: "100%",
                }}
              />
            </div>
          )}
      </div>
    </>
  );
};

export default LibraryStocktakeDetail;
