import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useState } from "react";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
import {
  useLibraryBookExcelExport,
  useLibraryBookReviewExcelExport,
  useLibraryCollectExcelExport,
  useLibraryEntryLogExcelExport,
  useLibraryIssueExcelExport,
} from "../../../../hooks/member_tab/library-api";
const LibraryExportModal = ({ onClose, openFilter, lid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    from: "",
    to: "",
  });

  const [libraryBookExcelExport] = useLibraryBookExcelExport();
  const [libraryCollectExcelExport] = useLibraryCollectExcelExport();
  const [libraryIssueExcelExport] = useLibraryIssueExcelExport();
  const [libraryEntryLogExcelExport] = useLibraryEntryLogExcelExport();
  const [libraryBookReviewExcelExport] = useLibraryBookReviewExcelExport();
  const onBookExport = () => {
    if (lid) {
      setDisabled((pre) => !pre);
      libraryBookExcelExport({
        lid: lid,
        from: state?.from ? ymdSlashDateFormat(state.from) : "",
        to: state?.to ? ymdSlashDateFormat(state.to) : "",
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  const onCollectExport = () => {
    if (lid && state?.from && state?.to) {
      setDisabled((pre) => !pre);
      libraryCollectExcelExport({
        lid: lid,
        from: state?.from ? ymdSlashDateFormat(state.from) : "",
        to: state?.to ? ymdSlashDateFormat(state.to) : "",
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  const onIssueExport = () => {
    if (lid && state?.from && state?.to) {
      setDisabled((pre) => !pre);
      libraryIssueExcelExport({
        lid: lid,
        from: state?.from ? ymdSlashDateFormat(state.from) : "",
        to: state?.to ? ymdSlashDateFormat(state.to) : "",
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onEntryLogExport = () => {
    if (lid && state?.from && state?.to) {
      setDisabled((pre) => !pre);
      libraryEntryLogExcelExport({
        lid: lid,
        from: state?.from ? ymdSlashDateFormat(state.from) : "",
        to: state?.to ? ymdSlashDateFormat(state.to) : "",
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onReviewBookExport = () => {
    if (lid && state?.from && state?.to) {
      setDisabled((pre) => !pre);
      libraryBookReviewExcelExport({
        lid: lid,
        from: state?.from ? ymdSlashDateFormat(state.from) : "",
        to: state?.to ? ymdSlashDateFormat(state.to) : "",
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onExport = () => {
    if (lid) {
      if (openFilter === "BOOK") onBookExport();
      else if (openFilter === "ISSUE") onIssueExport();
      else if (openFilter === "COLLECT") onCollectExport();
      else if (openFilter === "ENTRY_LOG") onEntryLogExport();
      else if (openFilter === "REVIEW") onReviewBookExport();
      else {
        return null;
      }
    }
  };

  const onForm = (value) => {
    setState((prev) => ({
      ...prev,
      from: slashDateFormat(value),
    }));
  };

  const onTo = (value) => {
    setState((prev) => ({
      ...prev,
      to: slashDateFormat(value),
    }));
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("export_library_excel")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormCalendarInput
              name="from"
              label={t("o__from")}
              placeholder={t("o__please_select_date")}
              onChange={onForm}
              value={state.from}
            />
            <FormCalendarInput
              name="to"
              label={t("o__to")}
              placeholder={t("o__please_select_date")}
              onChange={onTo}
              value={state.to}
            />

            <CreateButton label="export" onAction={onExport} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default LibraryExportModal;
